
.ol-card {
  background: #fff;
  font-size: 12.8px;
  font-size: 0.8rem;
  padding: 8px 16px;
  padding: 0.5rem 1rem;
  margin-bottom: 8px;
  margin-bottom: 0.5rem;
  border-radius: 2px;
  border: 1px solid #ccc;
}
.ol-card-title {
  font-size: 16px;
  font-size: 1rem;
  background: rgba(0,0,0,0.02);
  padding: 8px;
  padding: 0.5rem;
  margin: 0 -16px;
  margin: 0 -1rem;
  margin: -8px -16px 8px -16px;
  margin: -0.5rem -1rem 0.5rem -1rem;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}


