
.ol-form-item {
  position: relative;
  margin-bottom: 25.6px;
  margin-bottom: 1.6rem;
  vertical-align: middle;
}
.ol-form-item.ol-form-inline {
  display: inline-block;
}
.ol-form-item__label {
  float: left;
  text-align: left;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.2;
  color: #333;
  padding: 11px 12px 11px 0;
}
.ol-form-item__label.top {
  display: inline-block;
  float: none;
}
.ol-form-item__label.right {
  text-align: right;
}
.ol-form-item__content {
  min-width: 208px;
  min-width: 13rem;
  position: relative;
  line-height: 2.5;
  font-size: 16px;
  font-size: 1rem;
}
.ol-from-item--message {
  position: absolute;
  font-size: 12px;
  top: 36px;
  color: #f00;
}
.fade-enter-active, .fade-leave-active {
  transition: all 0.3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(0, -0.2rem, 0);
          transform: translate3d(0, -0.2rem, 0);
}


