
.ol-input-content {
  position: relative;
  width: 100%;
}
.ol-input {
  display: inline-block;
  height: 41.6px;
  height: 2.6rem;
  width: 100%;
  line-height: 41.6px;
  line-height: 2.6rem;
  text-align: left;
  vertical-align: middle;
  border: 1px solid transparent;
  border-radius: 4px;
  padding-left: 8px;
  color: #666;
}
.ol-input:focus, .ol-input:hover {
  border-color: transparent;
}
.ol-input.default {
  border-color: #d9d9d9;
}
.ol-input.primary {
  border-color: #57c5f7;
}
.ol-input.ghost {
  border-color: #d9d9d9;
}
.ol-input.warning {
  border-color: #fa0;
}
.ol-input.danger {
  border-color: #f50;
}
.ol-input.success {
  border-color: #87d068;
}
.ol-input.info {
  border-color: #2db7f5;
}
.ol-input.disabled {
  color: #888;
  background-color: #f9f9f9;
  border-color: #e7e7e7;
}
.ol-tip {
  position: relative;
  display: inline-block;
  padding: 4px 0;
  font-size: 12px;
  color: #f00;
  line-height: 1;
}


