
.ol-timeline-recourse {
  position: relative;
}
.ol-timeline {
  position: relative;
  width: 100%;
  height: 64px;
  height: 4rem;
  list-style: none;
  text-align: left;
  background-color: #fff;
  cursor: pointer;
  white-space: normal;
  padding: 6.4px 22.4px;
  padding: 0.4rem 1.4rem;
  line-height: 2.4;
  outline: none;
}
.ol-timeline-word {
  font-size: 12.8px;
  font-size: 0.8rem;
}
.ol-timeline-icon {
  background-color: #fff;
  position: relative;
  left: -9.6px;
  left: -0.6rem;
  z-index: 2;
  font-size: 19.2px;
  font-size: 1.2rem;
  padding-right: 16px;
  padding-right: 1rem;
}
.ol-timeline-line {
  display: inline-block;
  position: absolute;
  left: 19.6px;
  z-index: 1;
  top: 20px;
  width: 2px;
  height: 100%;
  background-color: #ccc;
}
.ol-timeline .warning {
  color: #fa0;
}
.ol-timeline .failed {
  color: #f50;
}
.ol-timeline .successs {
  color: #87d068;
}
.ol-timeline .info {
  color: #57c5f7;
}


