
.ol-tooltips {
  position: fixed;
  text-align: center;
  visibility: hidden;
  background-color: rgba(0,0,0,0.3);
  padding: 0 6px;
  font-size: 12.8px;
  font-size: 0.8rem;
  line-height: 2.4;
  border-radius: 4px;
  outline: none;
  color: #fff;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}


