
.ol-btn-group {
  display: inline-block;
}
.ol-btn-group .ol-btn {
  margin: 0 -2.5px;
}
.ol-btn-group .ol-btn:hover, .ol-btn-group .ol-btn:focus {
  position: relative;
}
.ol-btn-group>.ol-btn:first-child {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.ol-btn-group>.ol-btn:last-child {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
.ol-btn-group>.ol-btn:not(:first-child):not(:last-child) {
  border-radius: 0;
}


