
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
  overflow: hidden;
}
.clearfix {
  *zoom: 1;
}
.ol-datepick-section {
  position: relative;
}
.ol-datepick {
  position: absolute;
  top: 41.6px;
  top: 2.6rem;
  left: 3.2px;
  left: 0.2rem;
  width: 256px;
  width: 16rem;
  border: 1px solid rgba(0,0,0,0.1);
  background: #fff;
  z-index: 1111111;
}
.ol-datepick span {
  display: inline-block;
  float: left;
  height: 35.2px;
  height: 2.2rem;
  width: 35.2px;
  width: 2.2rem;
  text-align: center;
  line-height: 35.2px;
  line-height: 2.2rem;
  margin-bottom: 6.4px;
  margin-bottom: 0.4rem;
  cursor: pointer;
}
.ol-datepick span:hover, .ol-datepick span:active {
  background: #2db7f5;
}
.ol-datepick span.glay {
  color: #ccc;
}
.ol-datepick span.active {
  background: #2db7f5;
}
.ol-datepick .ol-chosedate {
  text-align: center;
  width: 100%;
}
.ol-datepick .ol-chosedate a {
  display: inline-block;
  width: 22%;
  line-height: 1;
  vertical-align: middle;
  height: 16px;
  height: 1rem;
}
.ol-datepick .ol-title span:hover {
  cursor: default;
  background: #fff;
}
.ol-content-close {
  position: relative;
  padding: 4px;
  line-height: 1;
  text-align: right;
}


