
.ol-cascader-container {
  position: relative;
  border: 1px solid #ccc;
  display: inline-block;
  min-width: 96px;
  min-width: 6rem;
  border-radius: 0.4rem;
  height: 38.4px;
  height: 2.4rem;
  line-height: 38.4px;
  line-height: 2.4rem;
  padding: 0 32px 0 8px;
  padding: 0 2rem 0 0.5rem;
  vertical-align: bottom;
}
.ol-cascader-container.disabled:hover {
  background: #efefef;
  border: 1px solid #ccc;
  color: #b7b7b7;
  cursor: not-allowed;
}
.ol-cascader-container:hover {
  border: 1px solid #2db7f5;
}
.ol-cascader-input {
  display: inline-block;
}
.ol-cascader-text {
  font-size: 12.8px;
  font-size: 0.8rem;
  line-height: 32px;
  line-height: 2rem;
}
.ol-cascader-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 32px;
  top: 2rem;
  left: 0;
  height: 160px;
  height: 10rem;
  border: 1px solid #e8e8e8;
  border-radius: 0.25rem;
  background: #fff;
  box-shadow: 0 1px 6px rgba(99,99,99,0.2);
  z-index: 2;
}
.ol-cascader-menu {
  min-width: 96px;
  min-width: 6rem;
  border-right: 1px solid #e8e8e8;
  overflow-y: auto;
}
.ol-cascader-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 1.6px 8px;
  padding: 0.1rem 0.5rem;
  width: 100%;
  font-size: 12.8px;
  font-size: 0.8rem;
  padding: 1.6px 8px;
  padding: 0.1rem 0.5rem;
  cursor: pointer;
  white-space: normal;
  text-overflow: ellipsis;
  overflow: hidden;
  transition: all 0.3s ease;
}
.ol-cascader-item:hover, .ol-cascader-item.active {
  background: #efefef;
}
.ol-cascader-arrow {
  position: absolute;
  top: 10px;
  right: 8px;
  right: 0.5rem;
  font-size: 6.4px;
  font-size: 0.4rem;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.ol-cascader-container.active .ol-cascader-arrow {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.ol-cascader-top .rd-cascader-content {
  bottom: 32px;
  bottom: 2rem;
  top: auto;
  top: initial;
}


