
.ol-breadcrumb-container {
  position: relative;
  height: 48px;
  height: 3rem;
  line-height: 48px;
  line-height: 3rem;
  padding: 2px;
  overflow: hidden;
}
.ol-breadcrumb {
  display: inline-block;
  cursor: pointer;
}
.ol-breadcrumb-world, .ol-breadcrumb-router {
  display: inline-block;
}
.ol-breadcrumb-router {
  text-align: right;
  width: 16px;
  width: 1rem;
  padding-right: 3.2px;
  padding-right: 0.2rem;
}


