
.ol-slider {
  position: relative;
  width: 98%;
}
.ol-slider .ol-slider-handle {
  position: absolute;
  top: -10px;
  left: 0px;
  height: 25.6px;
  height: 1.6rem;
  width: 25.6px;
  width: 1.6rem;
  border: 2px solid #57c5f7;
  border-radius: 50%;
  background-color: #fff;
}
.ol-slider .ol-slider-mark {
  display: inline-block;
  height: 9.6px;
  height: 0.6rem;
  width: 100%;
  background-color: #eee;
  line-height: 9.6px;
  line-height: 0.6rem;
  border-radius: 0.4rem;
}
.ol-slider .ol-slider-mark .ol-slider-show {
  display: inline-block;
  height: 100%;
  width: 100%;
  background-color: #57c5f7;
  line-height: 9.6px;
  line-height: 0.6rem;
  border-radius: 0.4rem 0 0 0.4rem;
}
.ol-slider-handle-tip {
  display: block;
  opacity: 1;
  position: absolute;
  bottom: 32px;
  bottom: 2rem;
  left: -7.2px;
  left: -0.45rem;
  width: 32px;
  width: 2rem;
  line-height: 2.4;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
  text-align: center;
  border-radius: 0.5rem;
  transition: all 0.2s;
}
.ol-slider-handle-tip::before {
  content: '';
  position: absolute;
  background: transparent;
  border: 6.4px solid transparent;
  border: 0.4rem solid transparent;
  border-top-color: rgba(0,0,0,0.5);
  bottom: -11.2px;
  bottom: -0.7rem;
  left: 50%;
  margin-left: -6.4px;
  margin-left: -0.4rem;
}


