
.ol-btn {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  border: 1px solid transparent;
  background-image: none;
  background-color: #fff;
  cursor: pointer;
  white-space: normal;
  padding: 3.2px 22.4px;
  padding: 0.2rem 1.4rem;
  font-size: 12.8px;
  font-size: 0.8rem;
  line-height: 2.4;
  border-radius: 4px;
  outline: none;
  transition: background 0.2s;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
.ol-btn.default {
  color: #666;
  background-color: #fff;
  border-color: #d9d9d9;
}
.ol-btn.primary {
  color: #fff;
  background-color: #57c5f7;
  border-color: #57c5f7;
}
.ol-btn.primary:hover, .ol-btn.primary:focus {
  background-color: #81d8ff;
  color: #fff;
}
.ol-btn.warning {
  color: #fff;
  background-color: #fa0;
  border-color: #fa0;
}
.ol-btn.warning:hover, .ol-btn.warning:focus {
  color: #fff;
  background-color: rgba(255,170,0,0.7);
  border-color: #fa0;
}
.ol-btn.danger {
  color: #fff;
  background-color: #f50;
  border-color: #f50;
}
.ol-btn.danger:hover, .ol-btn.danger:focus {
  color: #fff;
  border-color: #f50;
  background-color: rgba(255,85,0,0.7);
}
.ol-btn.success {
  background-color: #87d068;
  border-color: #87d068;
  color: #fff;
}
.ol-btn.success:hover, .ol-btn.success:focus {
  color: #fff;
  border-color: #87d068;
  background-color: rgba(135,208,104,0.7);
}
.ol-btn.info {
  color: #fff;
  border-color: #2db7f5;
  background-color: #2db7f5;
}
.ol-btn.info:hover, .ol-btn.info:focus {
  color: #fff;
  border-color: #2db7f5;
  background-color: rgba(45,183,245,0.7);
}
.ol-btn:focus, .ol-btn:hover {
  color: #57c5f7;
  background-color: #fff;
  border-color: #57c5f7;
}
.ol-btn.ol-btn-small {
  padding: 8px 9.6px;
  padding: 0.5rem 0.6rem;
  min-width: 0;
  min-width: initial;
  font-size: 12.8px;
  font-size: 0.8rem;
  line-height: 16px;
  line-height: 1rem;
  min-width: initial;
}
.ol-btn.ol-btn-large {
  padding: 3.2px 24px;
  padding: 0.2rem 1.5rem;
  font-size: 22.4px;
  font-size: 1.4rem;
}
.ol-btn-icon-only {
  font-size: 16px;
  font-size: 1rem;
  line-height: 32px;
  line-height: 2rem;
  vertical-align: baseline;
}
.ol-btn.ol-btn-icon {
  width: 32px;
  width: 2rem;
  min-width: 0;
  min-width: initial;
  border-radius: 50%;
  height: 32px;
  height: 2rem;
  line-height: 32px;
  line-height: 2rem;
  padding: 0;
}
.ol-btn.ol-btn-icon i {
  margin-right: 0;
}
@-webkit-keyframes loading {
from {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
.ol-btn-loading {
  display: inline-block;
  -webkit-animation: loading 1s infinite linear;
          animation: loading 1s infinite linear;
}
.ol-btn.ol-btn-disabled {
  color: #c3c3c3;
  background: #ececec;
  border-color: #cecece;
  cursor: not-allowed;
}
.ol-btn.ol-btn-disabled:hover {
  color: #a7a7a7;
  background: #ececec;
  border-color: #d9d9d9;
}
@-webkit-keyframes loading {
from {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}


