
.ol-perview-container {
  display: table;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.8);
  height: 100%;
  text-align: center;
  color: #fff;
  z-index: 99999999;
}
.ol-perview {
  display: table-cell;
  vertical-align: middle;
}
.ol-preview-close {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 32px;
  font-size: 2rem;
}
.ol-preview-img {
  max-width: 100%;
  max-height: 100%;
}
.ol-perview-title {
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 32px;
  font-size: 2rem;
}
.ol-preview-nav-left {
  left: 0;
}
.ol-preview-nav-left .ol-preview-nav-arrow {
  left: 0;
  margin-left: 32px;
  margin-left: 2rem;
}
.ol-preview-nav-right {
  right: 0;
}
.ol-preview-nav-right .ol-preview-nav-arrow {
  right: 0;
  margin-right: 32px;
  margin-right: 2rem;
}
.ol-preview-nav-arrow {
  position: absolute;
  top: 50%;
  padding: 0 8px;
  padding: 0 0.5rem;
  background: rgba(0,0,0,0);
  line-height: 32px;
  line-height: 2rem;
  border-radius: 0.2rem;
  font-size: 32px;
  font-size: 2rem;
}


