/* 
 * bootstrop nav 
 * 表单式导航或标签
 */
.ol-nav {
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  clear: both;
}
.ol-nav:after, .ol-nav:before {
  content: " ";
  display: table;
  clear: both;
}
.ol-nav >li {
  position: relative;
  display: block;
}
.ol-nav >li >span {
  position: relative;
  display: block;
  line-height: 1.428571429;
  padding: 10px 15px;
  text-decoration: none;
}
.ol-nav >li >span:hover, .ol-nav >li >span:focus {
  text-decoration: none;
  background-color: rgba(125,125,125,0.1);
}
.ol-nav >li .disabled >span {
  color: rgba(125,125,125,0.1);
}
.ol-nav >li .disabled >span:hover, .ol-nav >li .disabled >span:focus {
  color: rgba(125,125,125,0.1);
  text-decoration: none;
  background-color: transparent;
  cursor: not-allowed;
}
.ol-nav .open >span, .ol-nav .open >span:hover, .ol-nav .open >span:focus {
  background-color: #eee;
  border-color: #428bca;
}
.ol-nav-tabs {
  border-bottom: 1px solid #ddd;
}
.ol-nav-tabs >li {
  float: left;
  margin-bottom: -1px;
}
.ol-nav-tabs >li >span {
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 2px 2px 0 0;
}
.ol-nav-tabs .active >span, .ol-nav-tabs .active >span:hover, .ol-nav-tabs .active >span:focus {
  color: #555;
  background-color: #fff;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  cursor: default;
}
.ol-nav-pills > li {
  float: left;
  margin-bottom: -1px;
}
.ol-nav-pills > li >span {
  border-radius: 4px;
}
.ol-nav-pills > li + li {
  margin-left: 2px;
}
.ol-nav-pills .active >span, .ol-nav-pills .active >span:hover, .ol-nav-pills .active >span:focus {
  color: #fff;
  background-color: #428bca;
}
.ol-nav-stacked > li {
  float: none;
}
.ol-nav-stacked > li + li {
  margin-top: 2px;
  margin-left: 0;
}
.ol-nav-justified {
  width: 100%;
}
.ol-nav-justified >li {
  float: none;
}
.ol-nav-justified >li >span {
  text-align: center;
  margin-bottom: 5px;
}
.ol-nav-justified >.dropdown .dropdown-menu {
  top: auto;
  left: auto;
}
@media (min-width: 768px) {
.ol-nav-justified > li {
    display: table-cell;
    width: 1%;
}
.ol-nav-justified > li >span {
    margin-bottom: 0;
}
}
.nav-tabs-justified {
  border-bottom: 0;
}
.nav-tabs-justified >li>span {
  margin-right: 0;
}
.nav-tabs-justified {
  border-bottom: 0;
}
.nav-tabs-justified > li >span {
  margin-right: 0;
  border-radius: 2px;
}
.nav-tabs-justified > .active >span, .nav-tabs-justified > .active >span:hover, .nav-tabs-justified > .active >span:focus {
  border: 1px solid #333;
}
@media (min-width: 768px) {
.nav-tabs-justified > li >span {
    border-bottom: 1px solid #333;
    border-radius: 2px 2px 0 0;
}
.nav-tabs-justified > .active >span, .nav-tabs-justified > .active >span:hover, .nav-tabs-justified > .active >span:focus {
    border-bottom-color: #333;
}
}


