
.ol-alert {
  position: relative;
  width: 100%;
  margin-bottom: 3.2px;
  margin-bottom: 0.2rem;
  text-align: left;
  border: 0 solid transparent;
  background-image: none;
  background-color: #fff;
  cursor: pointer;
  white-space: normal;
  padding: 6.4px 22.4px;
  padding: 0.4rem 1.4rem;
  line-height: 2.4;
  border-radius: 4px;
  outline: none;
  transition: background 0.2s;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
.ol-alert-icon {
  display: table-cell;
  font-size: 32px;
  font-size: 2rem;
  padding-right: 16px;
  padding-right: 1rem;
  vertical-align: middle;
  line-height: 32px;
  line-height: 2rem;
}
.ol-alert-icon.samll {
  font-size: 22.4px;
  font-size: 1.4rem;
  line-height: 22.4px;
  line-height: 1.4rem;
}
.ol-alert-content {
  display: table-cell;
}
.ol-alert-content .ol-alert-title {
  font-weight: bolder;
}
.ol-alert-close {
  position: absolute;
  right: 9.6px;
  right: 0.6rem;
  top: 8px;
  top: 0.5rem;
}
.ol-alert.warning {
  color: #fff;
  background-color: #fa0;
  border-color: #fa0;
}
.ol-alert.warning:hover, .ol-alert.warning:focus {
  color: #fff;
  background-color: rgba(255,170,0,0.7);
  border-color: #fa0;
}
.ol-alert.failed {
  color: #fff;
  background-color: #f50;
  border-color: #f50;
}
.ol-alert.failed:hover, .ol-alert.failed:focus {
  color: #fff;
  border-color: #f50;
  background-color: rgba(255,85,0,0.7);
}
.ol-alert.success {
  background-color: #87d068;
  border-color: #87d068;
  color: #fff;
}
.ol-alert.success:hover, .ol-alert.success:focus {
  color: #fff;
  border-color: #87d068;
  background-color: rgba(135,208,104,0.7);
}
.ol-alert.info {
  color: #fff;
  border-color: #2db7f5;
  background-color: #2db7f5;
}
.ol-alert.info:hover, .ol-alert.info:focus {
  color: #fff;
  border-color: #2db7f5;
  background-color: rgba(45,183,245,0.7);
}
.ol-alert:focus, .ol-alert:hover {
  color: #57c5f7;
  background-color: #fff;
  border-color: #57c5f7;
}


