
.ol-modal-container {
  position: fixed;
  top: 0;
  right: 0;
  background-color: rgba(0,0,0,0.3);
  width: 100%;
  height: 100%;
  z-index: 999999999;
}
.ol-modal {
  position: relative;
  margin: 96px auto;
  margin: 6rem auto;
  width: 480px;
  width: 30rem;
  background-color: #fff;
  border: 1px solid #eaf8fe;
  border-radius: 4px;
}
.ol-modal-icon {
  position: absolute;
  top: 16px;
  top: 1rem;
  right: 16px;
  right: 1rem;
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 24px;
  line-height: 1.5rem;
}
.ol-modal-title {
  width: 100%;
  line-height: 3;
  padding: 3.2px 24px;
  padding: 0.2rem 1.5rem;
  font-size: 19.2px;
  font-size: 1.2rem;
  border: 1px solid #f3f3f3;
}
.ol-modal-content {
  width: 100%;
  word-wrap: break-word;
  word-break: normal;
  min-height: 128px;
  min-height: 8rem;
  line-height: 1.5;
  padding: 3.2px 24px;
  padding: 0.2rem 1.5rem;
  color: #333;
  border: 1px solid #f3f3f3;
}
.ol-modal-close {
  text-align: right;
  padding: 2px 12px;
  color: #ccc;
}
.modal-enter, .modal-leave-active {
  opacity: 0;
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
}
.modal-enter-active, .list-leave-active {
  transition: all 0.5s ease;
}


