
.ol-upload-container {
  display: inline-block;
}
.ol-upload-file {
  display: none !important;
}
.ol-upload-list-item {
  display: inline-block;
  position: relative;
  width: 80px;
  width: 5rem;
  height: 128px;
  height: 8rem;
  margin: 8px;
  margin: 0.5rem;
}
.ol-upload-list-item:hover {
  color: #57c5f7;
}
.ol-upload-list-item:hover .ol-upload-preview-action {
  opacity: 1;
}
.ol-upload-list-item:hover .ol-upload-preview-action .ol-upload-close {
  display: inline;
}
.ol-upload-list-item:first-child {
  margin-left: 0;
}
.ol-upload-preview-img-wrapper {
  display: inline-block;
  height: 80px;
  height: 5rem;
  width: 80px;
  width: 5rem;
  border: 1px solid #e0e0e0;
  border-radius: 0.4rem;
  padding: 8px;
  padding: 0.5rem;
  box-sizing: border-box;
}
.ol-upload-preview-img {
  width: 100%;
  height: 100%;
  background-size: cover;
}
.ol-upload-preview-info {
  display: inline-block;
  vertical-align: top;
  font-size: 12.8px;
  font-size: 0.8rem;
  width: 100%;
  text-align: center;
  overflow: hidden;
}
.ol-upload-preview-info-title, .ol-upload-preview-info-size {
  height: 16px;
  height: 1rem;
  line-height: 16px;
  line-height: 1rem;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}
.ol-upload-preview-action {
  position: absolute;
  width: 100%;
  height: 80px;
  height: 5rem;
  text-align: center;
  background-color: rgba(0,0,0,0.29);
  border-radius: 0.4rem;
  opacity: 0;
  transition: opacity 0.3s;
}
.ol-upload-close {
  display: none;
  color: #fff;
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 80px;
  line-height: 5rem;
}


