
.ol-checkbox-wrapper {
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  font-size: 1rem;
  line-height: 2;
}
.ol-checkbox-inner {
  position: relative;
  display: inline-block;
  height: 19.2px;
  height: 1.2rem;
  width: 19.2px;
  width: 1.2rem;
  border: 1px solid #e6e6e6;
  margin-right: 6.4px;
  margin-right: 0.4rem;
  border-radius: 4px;
}
.ol-checkbox-inner:hover {
  border: 1px solid #2db7f5;
}
.ol-checkbox-inner-icon {
  position: absolute;
  color: #fff;
  font-size: 12.8px;
  font-size: 0.8rem;
  left: 38%;
  top: 50%;
  width: 100%;
  height: 100%;
  margin-top: -4.8px;
  margin-top: -0.3rem;
  margin-left: -6.4px;
  margin-left: -0.4rem;
  line-height: 12.8px;
  line-height: 0.8rem;
  text-align: center;
  transition: background 0.2s;
}
.ol-checkbox-inner.selected {
  background: #2db7f5;
  border: 1px solid #2db7f5;
}
.ol-checkbox-inner.selected:hover {
  opacity: 0.8;
}
.ol-checkbox-input {
  opacity: 0;
}
.ol-checkbox-inner.disabled {
  background: #eaeaea;
}


