
.ol-pagination {
  display: inline-block;
  width: 32px;
  width: 2rem;
  height: 32px;
  height: 2rem;
  line-height: 2;
  text-align: center;
  border: 1px solid #57c5f7;
  background-image: none;
  background-color: #fff;
  cursor: pointer;
  white-space: normal;
  vertical-align: middle;
  text-indent: 5px;
  font-size: 16px;
  font-size: 1rem;
  border-radius: 2px;
  margin-right: 16px;
  margin-right: 1rem;
  outline: none;
  transition: background 0.2s;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
.ol-pagination.notborder {
  border: none;
}
.ol-pagination i {
  position: relative;
  top: 8px;
}
.ol-pagination.primary {
  color: #fff;
  background-color: #57c5f7;
  border-color: #57c5f7;
}
.ol-pagination.primary:hover, .ol-pagination.primary:focus {
  background-color: #81d8ff;
  color: #fff;
}
.ol-pagination:focus, .ol-pagination:hover {
  color: #57c5f7;
  background-color: #fff;
  border-color: #57c5f7;
}
.ol-pagination.active {
  color: #fff;
  background-color: #57c5f7;
}
.ol-pagination.ol-pagination-disabled {
  color: #c3c3c3;
  background: #ececec;
  border-color: #cecece;
  cursor: not-allowed;
}
.ol-pagination.ol-pagination-disabled:hover {
  color: #a7a7a7;
  background: #ececec;
  border-color: #d9d9d9;
}


