
.ol-notification-container {
  position: fixed;
  top: 16px;
  top: 1rem;
  right: 16px;
  right: 1rem;
  z-index: 9999;
}
.ol-notification {
  position: relative;
  width: 320px;
  width: 20rem;
  background-color: #fff;
  margin-right: 16px;
  margin-right: 1rem;
  border: 1px solid #eaf8fe;
  border-radius: 4px;
  padding: 16px 24px;
  padding: 1rem 1.5rem;
  margin-bottom: 16px;
  margin-bottom: 1rem;
}
.ol-notification.success, .ol-notification.warning, .ol-notification.faile, .ol-notification.info {
  padding-left: 64px;
  padding-left: 4rem;
}
.ol-notification.success .rd-notification-icon {
  color: #87d068;
}
.ol-notification.info .rd-notification-icon {
  color: #2db7f5;
}
.ol-notification.warning .rd-notification-icon {
  color: #fa0;
}
.ol-notification.failed .rd-notification-icon {
  color: #f50;
}
.ol-notification-title {
  font-size: 14.4px;
  font-size: 0.9rem;
}
.ol-notification-content {
  color: #999;
  font-size: 12.8px;
  font-size: 0.8rem;
  line-height: 1.5;
  margin: 0;
}
.ol-notification-close {
  position: absolute;
  top: 3.2px;
  top: 0.2rem;
  right: 8px;
  right: 0.5rem;
  font-size: 12.8px;
  font-size: 0.8rem;
  color: #ccc;
}
.ol-notification-close:hover {
  color: #969696;
}
.ol-notification-icon {
  position: absolute;
  top: 50%;
  font-size: 24px;
  font-size: 1.5rem;
  left: 16px;
  left: 1rem;
  margin-top: -12px;
  margin-top: -0.75rem;
  line-height: 24px;
  line-height: 1.5rem;
}
.notification-enter {
  opacity: 0;
}
.notification-enter-active, .notification-leave {
  transition: opacity 0.5s ease;
}
.notification-leave-active {
  opacity: 0;
  position: absolute;
}
.notification-move {
  transition: -webkit-transform 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.5s cubic-bezier(0.55, 0, 0.1, 1), -webkit-transform 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}


