
.ol-radio {
  line-height: 32px;
  line-height: 2rem;
  margin-right: 16px;
  margin-right: 1rem;
}
.ol-radio-inner {
  position: relative;
  height: 22.4px;
  height: 1.4rem;
  width: 22.4px;
  width: 1.4rem;
  top: 6.4px;
  top: 0.4rem;
  display: inline-block;
  border-radius: 50%;
  border: 1px solid #ccc;
}
.ol-radio-inner:hover {
  border: 1px solid #2db7f5;
}
.ol-radio-inner:after {
  position: absolute;
  display: inline-block;
  width: 12.16px;
  width: 0.76rem;
  height: 12.16px;
  height: 0.76rem;
  content: "";
  top: 4.1px;
  left: 4.3px;
  border-radius: 50%;
  background-color: #2db7f5;
  opacity: 0;
  transition: opacity 0.2s;
}
.ol-radio.disabled {
  color: #eaeaea;
}
.ol-radio.checked .ol-radio-inner:after {
  opacity: 1;
}
.ol-radio-text {
  top: 1px;
}


