
.ol-loading {
  margin: 2px 8px;
/*  float: left;    */
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border: 0.4em solid transparent;
  border-top: 3.2px solid #f3f3f3;
  border-top: 0.2rem solid #f3f3f3;
  border-right: 3.2px solid #f3f3f3;
  border-right: 0.2rem solid #f3f3f3;
  border-bottom: 3.2px solid #f3f3f3;
  border-bottom: 0.2rem solid #f3f3f3;
  border-left: 3.2px solid transparent;
  border-left: 0.2rem solid transparent;
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
.ol-loading, .ol-loading:after {
  border-radius: 50%;
  width: 3em;
  height: 3em;
}
@-webkit-keyframes load8 {
0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-webkit-keyframes load8 {
0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes load8 {
0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}


