
.ol-select-container {
  position: relative;
  border: 1px solid #ccc;
  display: inline-block;
  min-width: 96px;
  min-width: 6rem;
  border-radius: 0.4rem;
  height: 38.4px;
  height: 2.4rem;
  line-height: 38.4px;
  line-height: 2.4rem;
  padding: 0 32px 0 8px;
  padding: 0 2rem 0 0.5rem;
  vertical-align: bottom;
  width: 100%;
}
.ol-select-container.disabled:hover {
  background: #efefef;
  border: 1px solid #ccc;
  color: #b7b7b7;
  cursor: not-allowed;
}
.ol-select-container:hover {
  border: 1px solid #2db7f5;
}
.ol-select-options-container {
  position: absolute;
  width: 100%;
  padding: 0;
  top: 41.6px;
  top: 2.6rem;
  left: -1px;
  background-color: #fff;
  z-index: 2;
  box-shadow: rgba(0,0,0,0.118) 0px 1px 6px, rgba(0,0,0,0.118) 0px 1px 4px;
  border-radius: 2px;
  max-height: 320px;
  max-height: 20rem;
  overflow-y: auto;
}
.ol-select-option {
  padding: 0 8px;
  padding: 0 0.5rem;
  width: 100%;
  font-size: 12.8px;
  font-size: 0.8rem;
  padding: 1.6px 8px;
  padding: 0.1rem 0.5rem;
  cursor: pointer;
  white-space: normal;
  text-overflow: ellipsis;
  overflow: hidden;
  transition: all 0.3s ease;
  border-bottom: 1px solid #f1f1f2;
}
.ol-select-option:last-child {
  border-bottom: 1px sold transparent;
}
.ol-select-option.selected {
  background: #67cdfb;
  color: #fff;
}
.ol-select-option.disabled {
  background: #efefef;
  color: #b7b7b7;
  cursor: not-allowed;
}
.ol-select-option:hover {
  opacity: 0.7;
}
.ol-select-search-wrapper {
  top: 0;
  width: 100%;
}
.ol-select-search-input {
  width: 100%;
  padding: 0 8px;
  padding: 0 0.5rem;
  border: 0;
  opacity: 0.5;
  outline: none;
}
.fade-enter-active, .fade-leave-active {
  transition: all 0.3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(0, -1rem, 0);
          transform: translate3d(0, -1rem, 0);
}


