
.ol-switch-content.small {
  height: 25.6px;
  height: 1.6rem;
  width: 48px;
  width: 3rem;
}
.ol-switch-content.small .ol-switch-inner {
  top: 3.2px;
  top: 0.2rem;
  left: 3.2px;
  left: 0.2rem;
  height: 19.2px;
  height: 1.2rem;
  width: 19.2px;
  width: 1.2rem;
}
.ol-switch-content.small.open .ol-switch-inner {
  -webkit-transform: translate3d(1.4rem, 0, 0);
  transform: translate3d(1.4rem, 0, 0);
}
.ol-switch-content {
  position: relative;
  display: inline-block;
  height: 32px;
  height: 2rem;
  width: 64px;
  width: 4rem;
  background-color: #ddd;
  border-radius: 1.4rem;
  line-height: 32px;
  line-height: 2rem;
  margin-right: 3.2px;
  margin-right: 0.2rem;
  margin-bottom: 1.6px;
  margin-bottom: 0.1rem;
}
.ol-switch-content.open {
  background-color: #f00;
}
.ol-switch-content.open .ol-switch-inner {
  -webkit-transform: translate3d(2rem, 0, 0);
  transform: translate3d(2rem, 0, 0);
}
.ol-switch-inner {
  position: absolute;
  top: 3.2px;
  top: 0.2rem;
  left: 3.2px;
  left: 0.2rem;
  height: 25.6px;
  height: 1.6rem;
  width: 25.6px;
  width: 1.6rem;
  background-color: #fff;
  border-radius: 50%;
  transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.2s cubic-bezier(0.23, 1, 0.32, 1);
}


