
.ol-cut{
    position: relative;
    width: 1000px;
    height: 300px;
    background: url('../../img/transparent.jpg') repeat;
    margin: 20px 0;
    overflow: hidden;
    border: 4px solid rgba(0,0,0, .1)
}
.ol-cut__img--warp{
    transition: all 1s;
}
.ol-cut__img--warp img{
    width: 100%;
    height: 100%;
}
.ol-cut-mask{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0, .5);
}
.ol-cut__img-out{
    position: absolute;
    top: 30px;
    left: 40px;
    width: 100px;
    height: 100px;
    overflow: hidden;
}
.ol-cut__img-out span{
    display: inline-block;
    position: absolute;
    width: 8px;
    height: 8px;
    background: blue;
}
.ol-cut__img-out .ol-cut__anchor--leftTop{
    top: 0;
    left: 0;
}
.ol-cut__anchor--centerTop{
    top: 0;
    left: 50%;
}
.ol-cut__anchor--rightTop{
    top: 0;
    right: 0;
}
.ol-cut__anchor--righMiddle{
    top: 50%;
    right: 0;
}
.ol-cut__anchor--rightBottom{
    bottom: 0;
    right: 0;
}
.ol-cut__anchor--centerBottom{
    left: 50%;
    bottom: 0;
}
.ol-cut__anchor--leftBottom{
    left: 0;
    bottom: 0
}
.ol-cut__anchor--leftMiddle{
    top: 50%;
    left: 0;
}



