
.ol-table {
  width: 100%;
  max-width: 100%;
  text-align: left;
}
.ol-table-th {
  color: rgba(0,0,0,0.871);
  height: 48px;
  height: 3rem;
  border-bottom: 1px solid #e9e9e9;
}
.ol-table-td {
  position: relative;
  font-size: 12.8px;
  font-size: 0.8rem;
  text-align: left;
  padding: 16px 8px;
  padding: 1rem 0.5rem;
  text-align: center;
}
.ol-table-state {
  font-size: 12.8px;
  font-size: 0.8rem;
  padding: 3.2px 8px;
  padding: 0.2rem 0.5rem;
  background: #a5a5a5;
  color: #fff;
}
.ol-table-state.success {
  background-color: #87d068;
}
.ol-table-state.info {
  background-color: #2db7f5;
}
.ol-table-state.warning {
  background-color: #fa0;
}
.ol-table-state.failed {
  background-color: #f50;
}
.ol-table-td.sort-icon {
  padding-right: 24px;
  padding-right: 1.5rem;
}
.ol-table-td .ol-btn {
  margin-right: 8px;
  margin-right: 0.5rem;
}
.ol-table-check-col {
  padding-left: 8px;
  padding-left: 0.5rem;
}
.ol-table-sort-icon {
  position: absolute;
  top: 33.6px;
  top: 2.1rem;
  right: 8px;
  right: 0.5rem;
}
.ol-table-sort-icon.active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.tabel-avatar {
  height: 32px;
  height: 2rem;
  border-radius: 2rem;
}
@media screen and (max-width: 768px) {
.ol-table-td {
    padding: 0 0.5rem;
    font-size: 0.8rem;
}
}


