
.ol-collapse-item {
  position: relative;
}
.ol-collapse-item:last-child {
  border-bottom: 1px solid #f1f1f1;
}
.ol-collapse-item__header {
  padding: 0 9.6px;
  padding: 0 0.6rem;
  border: 1px solid #f1f1f1;
  border-bottom: 1px solid transparent;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 500;
}
.ol-collapse-item__wrap {
  padding: 9.6px;
  padding: 0.6rem;
  border: 1px solid #f1f1f1;
  border-bottom: 1px solid transparent;
  line-height: 2;
  font-size: 12.8px;
  font-size: 0.8rem;
}


